import React from "react";

const SentimentpostSkeleton = () => {
  return (
   
    [1,2,3].map(()=>{
      return(<div className="px-0 py-3 cursor-pointer border-bottom">
      <div className="d-flex">
        <div className="sk_line sk_img rounded-0 m-0 me-5"></div>
        <div className="d-flex flex-column w-100">
          <div className="d-flex m_grid_author w-100 mb-3">
            <div className="sk_line sk_img m-0 me-3"></div>
            <div className="w-50 me-10">
              <h3 className="sk_line sk_line_height_16 sk_line_90 m-0 mb-3"></h3>
              <p className="sk_line sk_line_height_10 sk_line_60 m-0"></p>
            </div>
            <div class="sk_line sk_line_10 sk_line_height_16 m-0 ms-10"></div>
          </div>
          <p className="sk_line sk_line_height_10 sk_line_100 m-0 mb-3"></p>
          <p className="sk_line sk_line_height_10 sk_line_60 m-0"></p>
        </div>
        <div className="sk_line sk_line_10 sk_line_height_16 mt-0 mb-0 ms-5"></div>
      </div>
    </div>)
    })
  );
};

export default React.memo(SentimentpostSkeleton);
